.form-checkbox {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #fff;
  border: 2px solid #333333;
  border-radius: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  cursor: pointer;
  height: 22px;
  left: 11px;
  position: absolute;
  top: 14px;
  width: 22px;
}

.form-checkbox:after {
  opacity: 1;
  border: solid 2px solid #333333;
  border-radius: 0;
  border-top-color: 2px solid #333333;
  border-width: 0 0 3px 3px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  content: "";
  height: 7px;
  left: 2px;
  opacity: 0;
  position: absolute;
  top: 4px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  width: 14px;
}

.form-checkbox::after {
  border: solid var(--ons-color-input-border);
  border-radius: 1px;
  border-top-color: var(--ons-color-input-bg);
  border-width: 0 0 3px 3px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  content: "";
  height: 6px;
  left: 2px;
  opacity: 0;
  position: absolute;
  top: 1px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-90deg);
  width: 14px;
}

