/* autoprefixer grid: on */
/* grid */
.grid {
  min-height: 100%;
  display: -ms-grid;
  display: grid;
  grid-template-columns: minmax(calc((100% - 767px) / 2), 1.25rem) repeat(4, 1fr) minmax(calc((100% - 767px) / 2), 1.25rem);
  grid-template-rows: auto auto minmax(4.375rem, 1fr) auto;
  overflow: hidden;
}

header,
main {
  grid-column: 2/6;
  background: #fff;
  z-index: 15;
}

header {
  grid-row: 1;
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

main {
  grid-row: 2;
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

nav {
  grid-column: 1/6;
}

.collections-pagination {
  grid-column: 1/6;
}

.headline, .content {
  grid-column: 1/5;
}

#left-spacer-row1 {
  background: #fff;
  grid-column: 1;
  z-index: 10;
}

#right-spacer-row1 {
  background: #fff;
  grid-column: 6;
  z-index: 10;
}

#left-spacer-row2 {
  background: #fff;
  grid-column: 1;
  grid-row: 2;
  z-index: 10;
}

#right-spacer-row2 {
  background: #fff;
  grid-column: 6;
  grid-row: 2;
  z-index: 10;
}

.shape1 {
  background: #FFD605;
  grid-column: 2;
  grid-row: 3;
  z-index: 2;
  mix-blend-mode: multiply;
}

.shape2 {
  grid-column: 1/7;
  grid-row: 2/4;
  background: #327214;
  margin: 0 0 0 -9.375rem;
  width: 150%;
  opacity: 0.4;
  height: 6.25rem;
  transform: rotate(-15deg);
  z-index: 3;
}

.shape3 {
  grid-column: 1/7;
  grid-row: 2/4;
  background: #9013FE;
  height: 100%;
  opacity: 0.5;
  width: 6.25rem;
  transform: skew(80deg);
  z-index: 1;
}

.shape4 {
  grid-column: 5/6;
  grid-row: 3;
  background: #9013FE;
  z-index: 2;
  transform: skewX(45deg);
  opacity: 0.9;
}

.shape5 {
  grid-column: 1/7;
  grid-row: 3;
  background: #D70000;
  margin: 0 0 0 -9.375rem;
  width: 150%;
  height: 0.3125rem;
  transform: rotate(-40deg);
  z-index: 5;
}

#footer-left-spacer {
  grid-column: 1;
  grid-row: 4;
  background: #333333;
  z-index: 10;
}

footer {
  grid-column: 2/6;
  grid-row: 4;
  padding: 1.875rem 0 1.875rem 0;
  background: #333333;
  z-index: 10;
}

#footer-right-spacer {
  background: #333333;
  grid-column: 6;
  grid-row: 4;
  grid-column: 6;
  grid-row: 4;
  z-index: 10;
}

/* columns and rows */
@media (min-width: 768px) {
  .grid {
    grid-template-columns: minmax(calc((100% - 768px) / 2), 1.25rem) repeat(12, 1fr) minmax(calc((100% - 768px) / 2), 1.25rem);
  }

  header, main, .content {
    display: -ms-grid;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  .article-summary {
